header{width:100%;height:5rem;background:linear-gradient(to bottom, #008CFF, #67abe2);border-bottom-left-radius:3rem;border-bottom-right-radius:3rem;display:flex;position:fixed;align-items:center;justify-content:center}header .filter{display:flex;flex-wrap:wrap;position:absolute;right:14%;color:#fff;font-size:1.2em;width:15%}header .filter label{width:100%;padding-bottom:0.2rem}header .filter select{width:100%;background-color:#fff;padding:0.5rem;font-size:1.1rem;border:none;border-radius:0.4rem}header .pokedex-title{background-color:#fff;margin:1rem 0  1rem 0;height:3rem;width:40%;border:2px solid #000;display:flex;align-items:center;justify-content:center}header .pokedex-title h1{font-weight:500}.wrapper{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:linear-gradient(to bottom, rgba(245,34,67,0.3), rgba(245,34,67,0.7))}@media (min-width: 2000px){header{height:8rem}.filter label{font-size:1.8rem}.filter select{font-size:1.9rem !important;height:3rem}}@media (max-width: 770px){.filter{right:5% !important;width:20% !important}.filter label{padding-bottom:0.2rem}.filter select{padding:0.5rem;font-size:1.1rem}}@media (max-width: 575px){header{height:5.5rem !important;width:100%}.filter{right:8% !important;width:100%}.filter label{font-size:0.9rem}.filter select{font-size:0.9rem !important}}@media (max-width: 320px){header{width:320px}}body,html,#root,.wrapper{color:#202020;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial,  'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';font-size:14px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0;min-height:100%}@media (max-width: 320px){body,html,#root,.wrapper{width:100%;height:100%;margin:0px;padding:0px;overflow-x:hidden}}*{outline:none}::-webkit-scrollbar{width:15px}::-webkit-scrollbar-track{background:#d4d4d4;box-shadow:0 0 2px rgba(0,0,0,0.2) inset}::-webkit-scrollbar-