.pokemon__info{background-color:#fff;display:flex;flex-wrap:wrap;position:-webkit-sticky;position:sticky;top:6rem;justify-content:center;margin-right:5rem;height:35rem;border:1px solid #363434;width:18%}.pokemon__info-img{width:100%;height:0.1rem;display:flex;margin:0.5rem 1rem 14rem 1rem;justify-content:center}.pokemon__info-img button{position:absolute;right:2%}.pokemon__info-img img{width:16rem;height:16rem}.pokemon__info-content{width:100%;height:100%}.pokemon__info-content-name{margin:2rem 0rem 1rem 0rem;text-align:center;font-weight:600;width:100%;height:2rem;font-size:2rem}.pokemon__info-content-table{width:100%}.pokemon__table{width:95%;display:flex;position:relative;left:0.5rem}.pokemon__table tr{border-top:1px solid #292727;display:flex;flex-wrap:wrap}.pokemon__table thead{width:50%;border-right:1px solid #292727;border-left:1px solid #292727}.pokemon__table thead th{border-bottom:1px solid #292727;width:100%}.pokemon__table tbody{width:50%;border-right:1px solid #292727}.pokemon__table tbody td{width:100%;border-bottom:1px solid #292727;display:flex;justify-content:center;width:100%}@media (min-width: 2000px){.pokemon__info{width:44rem;height:50rem;top:20%;right:5%}.pokemon__info button{font-size:3rem}.pokemon__info-img img{margin-top:2rem;width:16rem;height:16rem}.pokemon__info-content-name{padding-bottom:2rem;font-size:3rem}.pokemon__info-content-table{width:100%}.pokemon__info .pokemon__table{font-size:2rem}}@media (max-width: 1288px){.pokemon__info-img img{margin-top:2rem;width:12rem;height:13rem}}@media (max-width: 1040px){.pokemon__info-img img{margin-top:2rem;width:12rem;height:12rem}.pokemon__info-content-name{font-size:1.6rem}}@media (max-width: 990px){.pokemon__info{margin-right:0;width:21%}.pokemon__info-img img{margin-top:2rem;width:10rem;height:10rem}.pokemon__info-content-name{font-size:1.2rem}.pokemon__info .pokemon__table{font-size:0.76rem}}@media (max-width: 770px){.pokemon__info{width:22%}.pokemon__info-img img{margin-top:2rem;width:10re